

































































import Vue from "vue";
import IconVue from "../utils/Icon.vue";
import CardVue from "../utils/Card.vue";
export default Vue.extend({
  components: { IconVue, CardVue },
  computed: {
    work(): any[] {
      return this.$store.state.work;
    },
    projects(): any[] {
      return this.$store.state.projects;
    },
  },
});
