









import Vue from "vue";
import marked from "marked";
export default Vue.extend({
  props: {
    name: String,
    description: String,
    tags: Array as () => string[],
    start: String,
    end: String,
    link: String,
    github: String,
    id: String,
  },
  computed: {
    timeRange(): string {
      return `${this.toMonth(
        +this.start.substring(0, 2)
      )} ${this.start.substring(3)} to ${
        this.end == null
          ? "Present"
          : `${this.toMonth(+this.end.substring(0, 2))} ${this.end.substring(
              3
            )}`
      }`;
    },
    linkColor(): string {
      switch (this.tags[0]) {
        case "Typescript":
          return "blue";
        case "C++":
          return "green";
        case "Python":
          return "yellow";
        case "Java":
          return "red";
        default:
          return "track-color";
      }
    },
    markedDesc(): string {
      return marked(this.description, { breaks: true, gfm: true });
    },
    highlighted(): boolean {
      return this.$store.state.highlighted === this.id;
    },
  },
  methods: {
    toMonth(monthNumber: number) {
      return new Date(2020, monthNumber, 1).toLocaleString("default", {
        month: "long",
      });
    },
  },
});
