import EducationVue from "./pages/Education.vue";
import WorkVue from "./pages/Work.vue";
import ProjectsVue from "./pages/Projects.vue";
export default Vue.extend({
  components: {
    ThemeSwitcherVue,
    SummaryVue,
    ProjectsVue,
    EducationVue,
    WorkVue,
    TitleVue
  },
  computed: {
    darkMode(): boolean {
      return this.$store.state.darkMode;
    },
  },
});
