





import Vue from "vue";
export default Vue.extend({
  props: {
    iconScale: Number,
    linkColor:String,
    description:String
  },
  computed:{
      scale():number {
          return this.iconScale || 2;
      },
      margin():number {
          return (this.scale - 1) * 22;
      },
      color():string {
          return this.linkColor || "var(--fore-color)";
      }
  }
});
