











import Vue from "vue";
import IconVue from "../utils/Icon.vue";
import ProjectVue from "../experiences/Project.vue";
interface Project {
  id: string;
  name: string;
  description: string;
  tags: string[];
  start: string;
  end: string | null;
  link?: string;
  github?: string;
}
export default Vue.extend({
  components: { IconVue, ProjectVue },
  computed: {
    projects(): Project[] {
      return this.$store.state.projects;
    },
  },
});
