









import Vue from "vue";
import IconVue from "./Icon.vue";
export default Vue.extend({
  components: { IconVue },
  props: {
    cardTitle: String,
    content: String,
    iconScale: Number,
    linkColor: String,
    href: String,
  },
  computed: {
    color(): string {
      return this.linkColor || "var(--fore-color)";
    },
  },
  methods: {
    highlight() {
      if (this.href && this.href.startsWith("#"))
        this.$store.dispatch("highlight", this.href.substring(1));
    },
  },
});
