











import Vue from "vue";
import IconVue from "../utils/Icon.vue";
import WorkVue from "../experiences/Work.vue";
interface Work {
  id: string;
  company: string;
  role: string;
  description: string;
  start: string;
  end: string | null;
}
export default Vue.extend({
  components: { IconVue, WorkVue },
  computed: {
    work(): Work[] {
      return this.$store.state.work;
    },
  },
});
